import React from 'react';
import { ThemeProviderProps } from '@mui/material/styles/ThemeProvider';

// RTL support.
import rtlPlugin from 'stylis-plugin-rtl';
import { CacheProvider } from '@emotion/react';
import createCache from '@emotion/cache';
import { prefixer } from 'stylis';

export interface DirectionProviderProps {
	children: ThemeProviderProps['children'];
	rtl?: boolean;
}

export interface DirectionCacheProviderProps {
	children: React.ReactNode;
	isRTL?: boolean;
}

export const DirectionContext = React.createContext( false );

/**
 * @see https://mui.com/material-ui/guides/right-to-left/
 */
const cacheRtl = createCache( {
	// Define class names prefix.
	key: 'eui-rtl',
	stylisPlugins: [ prefixer, rtlPlugin ],
} );

const DirectionCacheProvider: React.FC<DirectionCacheProviderProps> = ( props ) => {
	if ( ! props.isRTL ) {
		return <>{ props.children }</>;
	}

	return <CacheProvider value={ cacheRtl }>{ props.children }</CacheProvider>;
};

const DirectionProvider: React.FC<DirectionProviderProps> = ( props ) => {
	const isRTL = !! props.rtl;

	return (
		<DirectionContext.Provider value={ isRTL }>
			<DirectionCacheProvider isRTL={ isRTL }>
				{ props.children }
			</DirectionCacheProvider>
		</DirectionContext.Provider>
	);
};

export default DirectionProvider;
